<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "createMemberComponent",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      poId: "",
      poName: "",
      obj: {},
      selectedItem: [],
      selectedItemNames: [],
      rules: {
        group: [{ required: true, message: "必填", trigger: "change" }],
        qualifications: [
          { required: true, message: "必填", trigger: "change" },
        ],
      },
      queryForm: {
        s_Id: "", // 承攬商編號
        s_Name: "", // 承攬商名稱
      },
      notYetMemberTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "s_Id",
          label: "承攬商編號",
        },
        {
          id: "s_Name",
          label: "承攬商名稱",
        },
        {
          id: "tel",
          label: "電話",
        },
        {
          id: "key_Man",
          label: "公司負責人",
        },
      ],
      addForm: {
        authorization: [], // 資格
        group: [], // 組別
      },
      addRules: {
        authorization: [{ required: true, message: "必填", trigger: "change" }],
        group: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableData: [],
      tableList: [],
      work_Group: {},
    };
  },
  computed: {},
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    async queryNotMember() {
      const obj = {
        PO_ID: this.poId,
        S_ID: this.queryForm.s_Id,
        S_NAME: this.queryForm.s_Name,
        IN_DATE1: null,
        IN_DATE2: null,
        OUT_DATE1: null,
        OUT_DATE2: null,
        F_AUTHORIZATION: null,
        S_AUTHORIZATION: null,
        ISWORK: null,
        ISNOTWORK: null,
      };
      this.queryMember(obj);
    },
    changeSelection(item) {
      this.selectedItem = item;
      this.selectedItemNames = this.selectedItem
        .map((e) => e.s_Name)
        .join(", ");
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    async addMember(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.addForm.authorization.includes("F")) {
            this.selectedItem.forEach((e) => (e.f_Authorization = "Y"));
          } else {
            this.selectedItem.forEach((e) => (e.f_Authorization = null));
          }
          if (this.addForm.authorization.includes("S")) {
            this.selectedItem.forEach((e) => (e.s_Authorization = "Y"));
          } else {
            this.selectedItem.forEach((e) => (e.s_Authorization = null));
          }
          this.selectedItem.forEach((e) => {
            e.po_Id = this.poId;
            e.workGroup_Id = this.work_Group.workgroup_Id;
          });
          const res = await this.$store.dispatch(
            "insMember",
            this.selectedItem
          );
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "新增成功",
            });
            this.queryMember(this.obj);
            this.$refs.addForm.resetFields();
            this.$store.commit("CURRENT_PAGE_INIT");
            this.$refs.TableComponent.$refs.multipleTable.clearSelection();
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async queryMember(obj) {
      const params = this.getURLNewParams(obj);
      const res = await this.$store.dispatch("queryMember", params);
      const data = res.data.data;
      this.tableData = JSON.parse(JSON.stringify(data.notMembers));
      this.tableData.forEach((e, index) => (e.index = index + 1));
      this.tableList = this.tableData.slice(0, 10);
      this.work_Group = data.work_Group;
    },
  },
  async mounted() {
    this.$store.state.currentPage = 1;
    this.poId = this.$route.params.po_Id;
    this.poName = this.$route.query.po_Name;
    this.obj = {
      PO_ID: this.poId,
    };
    this.queryMember(this.obj);
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./createMember.html"></template>
